$.toast().reset('all');
$.toast({
	text: 'this is toast',
	heading: '',
	showHideTransition: 'fade',
	allowToastClose: false, // 关闭按钮
	hideAfter: 3000, // 设置toast消失的时间
	loader: false, // 设置加载条
	loaderBg: '#9EC600', // 设置加载条颜色
	stack: 5,
	position: 'bottom-left', // toast位置
	bgColor: false,
	textColor: false,
	textAlign: 'center',
	icon: 'success', // 设置图标
	beforeShow: () => {},
	afterShown: () => {},
	beforeHide: () => {},
	afterHidden: () => {},
	onClick: () => {}
});



